.header {
  color: var(--color-white);
  background-color: var(--header-color);
  height: 230px;
  background-position: 50% 80%;
  background-repeat: no-repeat;
  background-size: contain;
  padding: 58px 71px 0;
}

.header--btndark {
  width: 100%;
  justify-content: right;
  display: flex;
}

.header--nav {
  width: 100%;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 35px;
  padding: 5px;
  display: flex;
}

.header__nav {
  gap: 40px;
  display: flex;
}

.header--search {
  width: 336px;
  border-bottom: .5px solid var(--color-white);
}

.header--library {
  justify-content: center;
  gap: 20px;
  display: flex;
}

.header__container {
  max-width: 1000px;
  height: 100%;
  flex-direction: column;
  align-items: center;
  margin-left: auto;
  margin-right: auto;
  display: flex;
}

.header-home {
  background-image: var(--header-image);
}

.header-library {
  background-image: var(--header-library);
}

.nav__item {
  color: var(--color-white);
  text-decoration: none;
}

.logo {
  font-family: Roboto;
  font-size: 30px;
  font-style: normal;
  font-weight: 500;
  display: block;
}

.menu {
  text-transform: uppercase;
}

.input__search {
  background: inherit;
  width: 307px;
  color: var(--color-white);
  background-color: var(--search-color);
  border: none;
}

.input__search:hover, .input__search:focus, .input__btn {
  background: inherit;
  border: none;
}

.library__btn {
  text-transform: uppercase;
  background: inherit;
  color: var(--color-white);
  border: 1px solid var(--color-white);
  width: 136px;
  border-radius: 5px;
  padding: 15px 0;
  font-size: 12px;
  font-style: normal;
  font-weight: 500;
  line-height: 16px;
}

.library__btn:hover, .library__btn:focus {
  background-color: var(--button-color);
  border: 1px solid var(--button-color);
}

.library__btn--modal {
  background-color: var(--color-white);
  color: var(--button-color);
  border: 1px solid var(--button-color);
}

.library__btn--modal:hover, .library__btn--modal:focus {
  background-color: var(--button-color);
  color: var(--color-white);
  border: 1px solid var(--button-color);
}

.active {
  border-bottom: 3px solid var(--button-color);
}

.movie__title, .movie__genre {
  padding-left: 10px;
  font-family: Roboto;
  font-size: 12px;
  font-weight: 500;
  line-height: 10px;
}

.movie__title {
  text-transform: uppercase;
  line-height: 8px;
}

.movie__genre {
  color: #ff6b08;
  line-height: 18px;
}

.movie__popular {
  color: #fff;
  background-color: #ff6b08;
  border-radius: 3px;
  margin-left: 5px;
  padding: 1px 3px;
}

@media (min-width: 1024px) {
  .header {
    height: 250px;
    max-width: 100%;
    padding-bottom: 50px;
  }

  .header__container {
    max-width: 1000px;
    height: 100%;
    flex-direction: column;
    align-items: center;
    margin-left: auto;
    margin-right: auto;
    display: flex;
  }
}

@media (min-width: 300px) and (max-width: 767px) {
  .header {
