.footer {
  color: var(--text-color);
  background-image: var(--footer-image);
  background-color: var(--footer-image);
  text-align: center;
  background-size: contain;
  align-items: center;
  padding: 40px 0;
}

.footer__copyright {
  color: var(--text-color);
  font-family: Roboto, sans-serif;
  font-size: 16px;
  font-weight: 400;
  line-height: 20px;
}

.footer__copyright br {
  display: none;
}

.footer__copyright svg {
  margin-left: 7px;
  margin-right: 7px;
  animation: Scale .7s infinite alternate;
}

.footer__copyright span {
  color: var(--button-color);
  font-weight: 500;
  position: relative;
}

.footer__copyright span:after {
  content: "";
  width: 0;
  height: 2px;
  background: var(--button-color);
  border-radius: 2px;
  transition: all .7s;
  display: block;
  position: absolute;
  bottom: 0;
  right: 0;
}

.footer__copyright span:hover:after {
  width: 100%;
  left: 0;
}

.footer__copyright span:hover {
  cursor: pointer;
}

