.backdrop {
  background: rgba(0, 0, 0, .2);
  transition: opacity .25s cubic-bezier(.4, 0, .2, 1), visibility .25s cubic-bezier(.4, 0, .2, 1);
  position: fixed;
  top: 0;
  bottom: 0;
  left: 0;
  right: 0;
  overflow-y: auto;
}

.backdrop.is-hidden {
  visibility: hidden;
  opacity: 0;
  pointer-events: none;
}

.modal__footer {
  background-color: var(--background-color);
  box-shadow: var(--box-shadow);
  border-radius: 4px;
  font-family: Roboto, sans-serif;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.modal__footer--title {
  text-transform: uppercase;
  text-align: center;
  letter-spacing: .1em;
}

.modal__footer-team {
  flex-wrap: wrap;
  justify-content: space-around;
  margin-top: -10px;
  margin-left: -10px;
  padding: 20px 50px;
  display: flex;
}

.modal__footer svg {
  cursor: pointer;
  width: 40px;
  height: 40px;
  stroke: var(--text-color);
  border: 1px solid rgba(0, 0, 0, 0);
  border-radius: 50%;
  margin: 10px;
  padding: 4px;
  transition: all .2s ease-in-out;
  position: absolute;
  top: 0;
  right: 0;
}

.modal__footer svg:hover {
  border: 1px solid var(--button-color);
  border-radius: 50%;
  transition: all .2s ease-in-out;
  fill: var(--button-color) !important;
}

.modal__footer-card {
  background-color: var(--card-color);
  text-align: center;
  box-shadow: var(--box-shadow);
  border-radius: 4px;
  margin-top: 10px;
  margin-bottom: 20px;
